.part1{
    margin-left: 100px;
}
.br{
    width: 450px;
    border-top: 2px solid rgb(54, 50, 50) ;
}
.p{
    color:  rgb(31, 119, 182);
    font-size: 1.5rem;
}
.contain{
    width: 50%;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    grid-gap: 10px;
}
label{
    color:  rgb(31, 119, 182);
    font-size: 1.2rem;
    margin-top: 50px;
    font-weight: bold;
  
    
}
.input{
    width: 300px;
    margin-top: 50px;
    background-color: #4ce990; 
    border: 1px solid black;
    border-radius: 7px;
}
.input:focus{
    background-color: whitesmoke;
}
.search{
    margin-top: 50px;
    font-size: 1.3rem;
    color: gray;
    background-color: #4ce990;
    width: 300px;
    height: 30px;
    border-radius: 10px;
    
}

.continerprograms{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr ;
    grid-gap: 30px;
}
.yearprogram{
    margin-left: 100px;
    transform: translateY(-40px);
}




/* PROFILES */


.nameProfiles{
    width: 150px;
    height: 150px;
    background-color:#4ce990 ;
}


@media (max-width: 800px){
    .contain{
        display: grid;
        grid-template-columns: 1fr ;
        grid-gap: 20px;
    }  
    .input{
        margin: 0px;
    }
    .continerprograms{
        display: grid;
        grid-template-columns: 1fr ;
        grid-gap: 30px;
    }
}